// src/components/FileExplorer.js

import React from "react";
import { CodeBlock, dracula } from "react-code-blocks";
import { cpp_fileexp_str } from "../data";


export default function FileExplorer() {
  return (
   <section id="FileExplorer">
       

       <div className="container mx-auto flex px-10 py-20 md:flex-row flex-col items-center">
           <div className="lg:flex-grow mx-auto md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-left mb-16 md:mb-0 items-center text-center">
             <h1 className="title-font sm:text-4xl text-3xl mb-4 font-medium text-white">
               File Explorer
               <br className="hidden lg:inline-block" />
             </h1>
             <p className="mb-8 leading-relaxed">
             This graphic file explorer was the final project for my CS 310 Operating Systems course. It is a tree-view explorer with icons 
             for the most common filetypes, allowing for expanded view, toggleable recursive file-viewing and opening of files with compatible extensions.
             My partner and I collaborated in an ubuntu virtual machine, and everything is built using C++ and the SDL2 visual library.
             <br/><br/>
             The source code can be viewed below. The full source is contained on github, which can be accessed <a href="https://github.com/bull1588/os-fileexplorer" target = "_blank" className="text-white"> here.</a>
             </p>
           </div>
             <div className="lg:max-w-lg lg:w-full md:w-1/2 w-5/6">
             <img
               className="object-cover object-center"
               alt="hero"
               src="./fileexplorer.png"
             />
           </div>
        </div>

        <div className="container mx-auto max-w-small max-h-screen flex px-10 py-10">
            <CodeBlock
            text={cpp_fileexp_str}
            language={"cpp"}
            showLineNumbers={true}
            theme={dracula}
            />
        </div>

   </section>
 );
}
