// src/components/Resume.js

import { DocumentIcon } from "@heroicons/react/solid";
import React, {useState} from "react";
import { Document, Page } from "react-pdf/dist/esm/entry.webpack";
import { resume } from "../data";

export default function Resume() {
  const [numPage, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  function onDocumentLoadSucces(numPages){
    setNumPages(numPage);
    setPageNumber(1);
  }
  
  return (
    <section id="resume">
      <div className="container px-5 py-10 mx-auto">
        <div className="text-center mb-20">
          <DocumentIcon className="w-10 inline-block mb-4" />
          <h1 className="sm:text-4xl text-3xl font-medium title-font text-white mb-4 mx-auto">
            Resume
          </h1>
        </div>
        <div class="grid place-items-center h-screen">
            <Document
          file="/resume.pdf"
          onLoadSuccess={onDocumentLoadSucces}
             className="mx-auto">
                <Page height="1000" pageNumber={pageNumber} />
            </Document>
        </div>
      </div>
      <div className="lg:flex-grow mx-auto md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-center mb-16 md:mb-0 px-12 py-10 items-center text-center">
             <p className="mb-8 leading-relaxed">
             
             <a href="/resume.pdf" className=" text-white hover:underline" target="_blank"> You can download a pdf copy of the resume by clicking here.</a>

             </p>
           </div>
    </section>
  );
}