// src/components/Paper.js

import { DocumentIcon } from "@heroicons/react/solid";
import React, {useState} from "react";
import { Document, Page } from "react-pdf/dist/esm/entry.webpack";
import { paper } from "../data";

export default function Paper() {
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  function onDocumentLoadSuccess(numPages){
    setNumPages(numPages);
    setPageNumber(1);
  }

  function changePage(offSet){
    setPageNumber(prevPageNumber => prevPageNumber + offSet);
  }

  function changePageBack(){
    changePage(-1)
  }

  function changePageNext(){
    changePage(+1)
  }
  
  return (
    <section id="Paper">
      <div className="container px-5 py-10 mx-auto">
        <div className="text-center mb-20">
          <DocumentIcon className="w-10 inline-block mb-4" />
          <h1 className="sm:text-4xl text-3xl font-medium title-font text-white mb-4 mx-auto">
            Ethics Essay
          </h1>
        </div>
            <div className="grid place-items-center h-screen">
                <Document file="/paper.pdf" onLoadSuccess={onDocumentLoadSuccess}>
                  <Page height="850" pageNumber={pageNumber} />
                </Document>

                <button onClick={changePageBack} className="inline-flex items-center bg-gray-800 border-0 py-1 px-3 focus:outline-none hover:bg-gray-700 rounded text-base mt-4 md:mt-0">Previous Page</button>
                <button onClick={changePageNext} className="inline-flex items-center bg-gray-800 border-0 py-1 px-3 focus:outline-none hover:bg-gray-700 rounded text-base mt-4 md:mt-0">Next Page</button>
           </div>
      </div>
      <div className="lg:flex-grow mx-auto md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-center mb-16 md:mb-0 px-12 py-10 items-center text-center">
             <p className="mb-8 leading-relaxed">
             This is a paper from the core-curriculum Ethics course at the University of St. Thomas concerning the topic of moral relativism,
             descriptive relativism and how they differ. The relevance of ethics in the field of Computer Science is as significant as it is in any
             other facet of day-to-day life, if not more. 
             <br/><br/>
             I selected this essay as I believe it illustrates the most important consideration a software engineer ought
             to make while designing and programming. In a post-internet world, everything is interconnected to such a degree that
             understanding and navigating cultural divides is more difficult than ever. Appeals to the objective value or inherent truth
             of one's ideals and culture will be a constant no matter where you work, so one must always remain vigilant and do their best
             to combat bias. Culture and the difference therein will always affect software design, and it is up to us how we decide to interact
             with those ideas.
             <br/><br/>
             <a href="/paper.pdf" className=" text-white hover:underline" target="_blank"> You can download a pdf of the paper by clicking here.</a>

             </p>
           </div>
    </section>
  );
}