// src/components/About.js

import React from "react";

import { Link } from "react-router-dom";

export default function About() {
  return (
   <section id="about">
     <div className="container mx-auto flex px-10 py-20 md:flex-row flex-col items-center">
       <div className="lg:flex-grow md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-left mb-16 md:mb-0 items-center text-center">
         <h1 className="title-font sm:text-4xl text-3xl mb-4 font-medium text-white">
           Hi, I'm Jonas.
           <br className="hidden lg:inline-block" />
         </h1>
         <p className="mb-8 leading-relaxed">
           I'm a computer science undergrad from the University of St. Thomas. I've been working with, on and around computers my whole life, accumulating a ton of skills and experiences.
           I've developed and honed a wide variety of proficiencies throughout the field of computer science - from game development, to database engineering, to 3D graphical programming and web development. I'm comfortable
           tackling any task and confident I can learn and master it in no time.
           <br/><br/>
           Use the buttons below or the navbar at the top to see the content included in this portfolio. 
         </p>
         <div className="flex justify-center"> 
           <Link to="/resume" className="inline-flex text-white bg-green-500 border-0 py-2 px-6 focus:outline-none hover:bg-green-600 rounded text-lg">
              <button variant="outlined">
                Resume
              </button>
            </Link>
            <Link to="/fileexplorer" className="ml-4 inline-flex text-gray-400 bg-gray-800 border-0 py-2 px-6 focus:outline-none hover:bg-gray-700 hover:text-white rounded text-lg">
              <button variant="outlined">
                File Explorer
              </button>
            </Link>
            <Link to="/aiproj" className="ml-4 inline-flex text-gray-400 bg-gray-800 border-0 py-2 px-6 focus:outline-none hover:bg-gray-700 hover:text-white rounded text-lg">
              <button variant="outlined">
                AI Implementations
              </button>
            </Link>
            <Link to="/paper" className="ml-4 inline-flex text-gray-400 bg-gray-800 border-0 py-2 px-6 focus:outline-none hover:bg-gray-700 hover:text-white rounded text-lg">
              <button variant="outlined">
                Ethics
              </button>
            </Link>
            <Link to="/reflection" className="ml-4 inline-flex text-gray-400 bg-gray-800 border-0 py-2 px-6 focus:outline-none hover:bg-gray-700 hover:text-white rounded text-lg">
              <button variant="outlined">
                Reflection
              </button>
            </Link>

         </div>
       </div>
       <div className="lg:max-w-lg lg:w-full md:w-1/2 w-5/6">
         <img
           className="object-cover object-center rounded"
           alt="hero"
           src="./me.png"
         />
       </div>
     </div>
   </section>
 );
}
