import React from 'react';
import { Routes, Route } from 'react-router-dom';

import About from "./About";
import Contact from "./Contact";
import Navbar from "./Navbar";
import Cprojects from "./Cprojects";
import Resume from "./Resume";
import Reflection from "./Reflection";
import Skills from "./Skills";
import Testimonials from "./Testimonials";
import FileExplorer from "./FileExplorer";
import NavbarDeep from "./NavbarDeep";
import AiProjects from "./AiProjects";
import Paper from "./Paper";

const Main = () => {
  return (
    <Routes>
      <Route path='/' element={<><Navbar /><About /><Contact /></>}></Route>
      <Route path='/resume' element={<><NavbarDeep /><Resume /></>}></Route>
      <Route path='/fileexplorer' element={<><NavbarDeep /><FileExplorer /></>}></Route>
      <Route path='/aiproj' element={<><NavbarDeep /><AiProjects /></>}></Route>
      <Route path='/paper' element={<><NavbarDeep /><Paper /></>}></Route>
      <Route path='/reflection' element={<><NavbarDeep /><Reflection /></>}></Route>
      
    </Routes>
  );
}

export default Main;