// src/components/Navbar.js

import { ArrowRightIcon } from "@heroicons/react/solid";
import React from "react";

import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';

import { Link } from "react-router-dom";

const options = [
  'File Explorer', 'AI Implementations', 'Ethics & Applicability'
];
const defaultOption = options[0];

export default function Navbar() {
  return (
    <header className="bg-gray-800 md:sticky top-0 z-10">
      <div className="container mx-auto flex flex-wrap p-5 flex-col md:flex-row items-center">
        <a className="title-font font-medium text-white mb-4 md:mb-0">
          <a href="#about" className="ml-3 text-xl">
            Jonas Bull
          </a>
        </a>
        <nav className="md:mr-auto md:ml-4 md:py-1 md:pl-4 md:border-l md:border-gray-700	flex flex-wrap items-center text-base justify-center">
            <Link to="/resume" className = "mr-5 hover:text-white">
              <button variant="outlined">
                Resume
              </button>
            </Link>
            <Link to="/fileexplorer" className = "mr-5 hover:text-white">
              <button variant="outlined">
                File Explorer
              </button>
            </Link>
            <Link to="/aiproj" className = "mr-5 hover:text-white">
              <button variant="outlined">
                AI Implementations
              </button>
            </Link>
            <Link to="/paper" className = "mr-5 hover:text-white">
              <button variant="outlined">
                Ethics
              </button>
            </Link>
            <Link to="/reflection" className = "mr-5 hover:text-white">
              <button variant="outlined">
                Reflection
              </button>
            </Link>
        </nav>

        <a
          href="#contact"
          className="inline-flex items-center bg-gray-800 border-0 py-1 px-3 focus:outline-none hover:bg-gray-700 rounded text-base mt-4 md:mt-0">
          Contact Me
          <ArrowRightIcon className="w-4 h-4 ml-1" />
        </a>
      </div>
    </header>
  );
}
